@import url(https://fonts.googleapis.com/css2?family=M+PLUS+1+Code:wght@300;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{box-sizing:border-box}.App{text-align:center;font-family:'M PLUS 1 Code', sans-serif}.App .App-header{background-color:#F2F2F2;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:#0D0D0D}.App .App-header #drum-machine{display:flex;justify-content:space-between;align-items:center;padding:20px;width:55vw;background-color:#8C8C8C;border:1vmin solid #A61F2B}@media (max-width: 600px){.App .App-header #drum-machine{flex-wrap:wrap;width:80vw;text-align:center}}.App .App-header #drum-machine .flex-container{width:26vw;height:40vh;display:flex;flex-wrap:wrap;justify-content:space-between;align-content:space-between}@media (max-width: 600px){.App .App-header #drum-machine .flex-container{width:60vw;height:60wh}}.App .App-header #drum-machine .flex-container .drum-pad{width:8vw;height:10vh;padding-top:2vmin;background-color:#F2F2F2;color:#A60303;border-radius:5px;border:solid 2px #A60303;box-shadow:4px 4px 5px #0D0D0D}@media (max-width: 600px){.App .App-header #drum-machine .flex-container .drum-pad{width:18vw;height:10vh;padding-top:3vmin;font-size:calc(10px + 4vmin)}}.App .App-header #drum-machine .flex-container .drum-pad:active{color:#F2F2F2;background-color:#A61F2B;padding-top:calc(2vmin - 8px);border:solid 10px #0D0D0D;box-shadow:none}@media (max-width: 600px){.App .App-header #drum-machine .flex-container .drum-pad:active{padding-top:calc(5vmin - 8px)}}.App .App-header #drum-machine .controllers{display:flex;flex-wrap:wrap;justify-content:center;padding:0 auto;width:20vw;height:20vh;margin-top:auto;margin-bottom:auto}@media (max-width: 600px){.App .App-header #drum-machine .controllers{width:55vw;margin-top:5vh;margin-right:auto;margin-left:auto}}.App .App-header #drum-machine .controllers .toggle{width:40vw}.App .App-header #drum-machine .controllers .toggle .switch{position:relative;display:inline-block;width:60px;height:34px}.App .App-header #drum-machine .controllers .toggle .switch input{opacity:0;width:0;height:0}.App .App-header #drum-machine .controllers .toggle .slider{position:absolute;cursor:pointer;top:0;left:0;right:0;bottom:0;background-color:#ccc;transition:.4s}.App .App-header #drum-machine .controllers .toggle .slider:before{position:absolute;content:"";height:26px;width:26px;left:4px;bottom:4px;background-color:white;transition:.4s}.App .App-header #drum-machine .controllers .toggle input:checked+.slider{background-color:#A61F2B}.App .App-header #drum-machine .controllers .toggle input:focus+.slider{box-shadow:0 0 5px #0D0D0D}.App .App-header #drum-machine .controllers .toggle input:checked+.slider:before{transform:translateX(26px)}.App .App-header #drum-machine .controllers .toggle .slider.round{border-radius:34px}.App .App-header #drum-machine .controllers .toggle .slider.round:before{border-radius:50%}.App .App-header #drum-machine .controllers #display{width:14vw;height:7vh;margin-top:auto;padding-top:10px;background-color:#A61F2B;font-size:calc(10px + 1.5vmin)}@media (max-width: 600px){.App .App-header #drum-machine .controllers #display{width:45vw;font-size:calc(10px + 2vmin)}}

